import styles from "./sobre.module.css"
import {FaUserGraduate} from "react-icons/fa";

export default function Sobre(){
    return(
      <section className={styles.sobre} id="sobre">
        <h2 className={styles.titulo}><FaUserGraduate style={{paddingBottom: 6}}/> Um pouco sobre mim</h2>
        <div className={styles.sobre__container}>
          <div className={styles.sobre__texto}>
              <h3><span></span>Engenheiro mecnico {"&"} desenvolvedor full stack</h3>
              <p>Audodidata, polmata e apaixonado por tecnologia e em descobrir como elas funcionam, desde criana desmontava coisas pra entender como eram feitas.
                Aos 14 anos comecei a criar pginas na web atravs do HPG e Geocities. Para me aperfeioar, iniciei a graduao em Sistemas para Internet,
                  na Unifacs, onde fui apresentado ao mundo da programao. E foi amor a primeira vista! Apesar de amar o que fazia, decidi mudar de curso, mas continuei
                  estudando por conta prpria. Ingressei na graduao de engenharia mecnica, na UFBA e sempre que possvel inclua matrias de programao na grade curricular.
                  Aps me formar, decidi retornar de uma vez a rea que sempre fez meu corao bater mais forte. Me aperfeioando cada dia mais, estou sempre em busca
                    de aprender novas teconologias e masterizar as que j utilizo!
              </p>
          </div>
          <div className={styles.sobre__imagem}></div>
        </div>  
      </section>
    )
}